import React from 'react';

import {
  SectionList,
  TitledContent,
  TitledContentFC,
} from '../../Components/ContentLayout';

import CodeSample from '../../Components/common/CodeSample';

const sections = [] as TitledContent[];

const Introduction: TitledContentFC = ({
  Container,
}) => (
  <Container>
    <p>
      En fait, en <i>TypeScript</i>, un type et une interface
      c&apos;est  peu de choses prs la mme chose.
    </p>
    <p>
      La diffrence principale vient de la faon dont on peut
      combiner ces deux &quot;types de types&quot;:
    </p>
    <p>
      Une interface peut tendre une autre interface,
      tandis qu&apos;un type ne peut ni tendre un autre type
      ni tendre une interface.
    </p>
    <p>
      Les types peuvent se combiner les uns avec les autres,
      soit par union, soit par intersection.
    </p>
    <p>
      Cette distinction de comportement entre types et interface
      reflte bien les fameux paradigmes de l&apos;extension
      de fonctionnalit - qu&apos; on oppose souvent - que sont
      la composition et l&apos; hritage. <i>TypeScript</i> propose
      des constructions pour chacun des deux clans.
    </p>
  </Container>
);

sections.push({
  title: 'Introduction',
  anchor: 'types-de-types',
  Content: Introduction,
});

const Interfaces: TitledContentFC = ({
  Container,
}) => (
  <Container>
    <p>
      Les interfaces en <i>TypeScript</i> sont, je pense, similaires aux interfaces
      dans tous les langages qui ont des interfaces&nbsp;: elles dcrivent
      la &quot;forme&quot; que peut prendre un objet, une classe, une fonction...
      mais elle n&apos;en dfinissent pas l&apos;implmentation.
    </p>
    <p>
      Une interface peut tendre une autre interface, en voici un exemple&nbsp;:
    </p>
    <CodeSample title={'"TextNode" tend "Node"'}>
      {`
        interface Node {
          type: string
        }

        interface TextNode
          extends Node {
            type: 'TextNode'
            value: string
          }
      `}
    </CodeSample>
    <p>
      Cela dit, la notion d&apos;extension ou d&apos;implmentation en<i>TypeScript</i>&nbsp;
      est un peu diffrente d&apos;autres langages comme le C++ ou le PHP par exemple.
    </p>
    <p>
      En effet on pourrait dire qu&apos;en <i>TypeScript</i>&nbsp;
      tout tend  peu prs tout, dans la mesure o mme si
      on type les paramtres d&apos;une fonction,
      on pourra <strong>lui passer n&apos;importe quel type jug compatible</strong>&nbsp;
      par <i>TypeScript</i>.
    </p>
    <CodeSample title="Exemple d&apos;usage d&apos;un mauvais type, mais compatible">
      {`
        type Rectangle = {
          width: number
          height: number
        }

        type Square = {
          width: number
          height: number
        }

        const computeArea = (rect: Rectangle) =>
          rect.width * rect.height;

        const square1: Square = {
          width: 10,
          height: 10,
        };

        console.log(computeArea(square1));
      `}
    </CodeSample>
    <p>
      En PHP si vous dclarez que votre fonction a besoin d&apos;un
      paramtre implmentant telle interface, vous ne pourrez utiliser
      cette fonction qu&apos;avec un paramtre implmentant explicitement
      l&apos;interface attendue.
    </p>
    <p>
      <strong>
        En <i>TypeScript</i>, en gros, tant que l&apos;analyse statique
        du code conclut que a va marcher, vous faites ce que
        vous voulez.
      </strong>
    </p>
  </Container>
);

sections.push({
  title: 'Une interface peut en tendre une autre',
  anchor: 'interface-extend',
  Content: Interfaces,
});

const Types: TitledContentFC = ({
  Container,
}) => (
  <Container>
    <p>
      Les types en <i>TypeScript</i> peuvent se combiner
      par union avec l&apos;oprateur &quot;|&quot;, ou
      par intersection avec l&apos;oprateur &quot;&amp;&quot;, pour crer
      des types plus complexes.
    </p>
    <CodeSample title="Exemples d'intersection et d'union de types">
      {`
        type Person = {
          name: string
        }

        type Traveller = Person & {
          hasPassport: true
        }

        type Robot = {
          CPUs: number
          RAM: number
          version: string
        }

        type Employee = Person | Robot
      `}
    </CodeSample>

    <p>
      Quand on fait une intersection de deux types, on dclare
      que le type rsultant aura chacune des proprits des
      deux types, en mme temps.
    </p>

    <p>
      Quand on fait une union de deux types, on dclare que le
      type rsultant aura un sous-ensemble de l&apos;union
      des proprits des deux types.
      <strong>Mais pas n&apos;importe quel sous-ensemble</strong>, un
      objet, pour appartenir  une union, doit avoir au moins toutes
      les proprits de l&apos;un des types de l&apos;union. On
      verra a dans l&apos;exemple d&apos;aprs.
    </p>

    <CodeSample title="Union de types">
      {`
        interface Rectangle {
          width: number
          height: number
        }

        const computeArea = (rect: Rectangle) =>
          rect.width * rect.height;

        type hasWidth = {
          width: number
        }

        type hasHeight = {
          height: number
        }

        type UnionSquare = hasWidth | hasHeight;

        const unionSquareW: UnionSquare = {
          width: 15,
        };

        const unionSquareH: UnionSquare = {
          height: 15,
        };

        const unionSquare: UnionSquare = {
          width: 15,
          height: 32,
        };

        /**
         * La ligne qui suit provoque l'erreur suivante :
         *
         * Argument of type 'UnionSquare' is not assignable to
         * parameter of type 'Rectangle'. Property 'height'
         * is missing in type 'hasWidth' but
         * required in type 'Rectangle'.
         */

        console.log(
          computeArea(unionSquare),
        );

        type InterSquare = hasWidth & hasHeight

        const interSquare: InterSquare = {
          width: 30,
          height: 30,
        };

        /**
         * Mais a, a va trs bien :
         */

        console.log(
          computeArea(interSquare),
        );
      `}
    </CodeSample>

    <p>
      Comme je le disais plus haut:&nbsp;pour tre mmbre
      d&apos;une union de types A et B, on objet peut avoir
      n&apos;importe laquelle des proprits de A ou de B, mais il
      doit au moins avoir soit toutes les proprits de A, soit
      toutes les proprits de B.
    </p>

    <CodeSample title="Un membre d&apos;une union doit correspondre  au moins un type">
      {`
        type hasWidth = {
          width: number
        }

        type hasHeight = {
          height: number
          unitOfMeasurement: string
        }

        type UnionSquare = hasWidth | hasHeight;

        const unionSquareW: UnionSquare = {
          width: 15,
        };

        /**
         * La ligne qui suit provoque l'erreur suivante :
         *
         * Type '{ height: number; }' is not assignable to type
         * 'UnionSquare'. Property 'unitOfMeasurement'
         * is missing in type '{ height: number; }'
         * but required in type 'hasHeight'.
         */

        const unionSquareH: UnionSquare = {
          height: 15,
        };
      `}
    </CodeSample>

    <p>
      Enfin, a va de soi, mais un membre d&apos;une union
      de types ne peut pas avoir de proprits qui ne sont dans
      aucun des types de l&apos;union.
    </p>

    <CodeSample title="Les proprits doivent tre dfinies sur au moins un type">
      {`
        type hasWidth = {
          width: number
        }

        type hasHeight = {
          height: number
        }

        type UnionSquare = hasWidth | hasHeight;

        /**
         * La ligne qui suit provoque l'erreur suivante :
         *
         * Type '{ width: number; somethingElse: boolean; }'
         * is not assignable to type 'UnionSquare'.
         * Object literal may only specify known properties,
         * and 'somethingElse'
         * does not exist in type 'UnionSquare'.
         */

        const union: UnionSquare = {
          width: 15,
          somethingElse: true;
        };
      `}
    </CodeSample>
  </Container>
);

sections.push({
  title: 'Union et intersection de types',
  anchor: 'types-union-intersection',
  Content: Types,
});

export const TypesVSInterfacesPage: TitledContentFC = ({
  Container,
  H1,
}) => (
  <Container>
    <SectionList depth={H1.depth} sections={sections} />
  </Container>
);

const TypesVSInterfacesContent: TitledContent = {
  anchor: 'types-vs-interfaces',
  documentTitle: 'Types vs. Interfaces en TypeScript',
  title: 'Types vs. interfaces',
  Content: TypesVSInterfacesPage,
};

export default TypesVSInterfacesContent;
